* {
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Helvetica Neue", Helvetica, Arial, sans-serif;
  color: #e8ecf4;
  background: #1a1d29;
}

a {
  color: inherit;
}

.stripe {
  padding: 5rem 0;
}
.stripe .container {
  width: min(100% - 2rem, 1400px);
  margin-inline: auto;
  text-align: center;
}
.stripe h2 {
  font-size: clamp(1.6rem, 1.5rem + 1vw, 2.2rem);
  margin: 0 0 1rem;
}
.stripe p {
  color: #8b95a8;
  line-height: 1.7;
}

.site-header {
  position: sticky;
  top: 0;
  z-index: 1000;
  background: rgba(0, 0, 0, 0.9);
  backdrop-filter: saturate(120%) blur(8px);
  border-bottom: 1px solid rgba(99, 102, 241, 0.3);
}

.nav {
  width: min(100% - 2rem, 1400px);
  margin-inline: auto;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  height: 120px;
  transition: height 0.25s ease, padding 0.25s ease;
}
.nav.shrink {
  height: 80px;
}

.nav .nav-links .nav-link {
  transition: all 0.3s ease;
}

.nav.shrink .nav-links .nav-link {
  font-size: clamp(0.8rem, .7rem + .2vw, 1rem);
  padding: 0.4rem 0.7rem;
}

.nav-links {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
  gap: 0.5rem;
  align-items: center;
  flex-wrap: wrap;
}
.nav-links .nav-link {
  text-decoration: none;
  padding: 0.5rem 0.8rem;
  border-radius: 4px;
  font-size: clamp(0.9rem, 0.8rem + .2vw, 1.1rem);
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.3px;
  color: #ffffff;
  white-space: nowrap;
  transition: all 0.3s ease;
}
.nav-links .nav-link:hover {
  color: #ffffff;
  background: rgba(255, 255, 255, 0.15);
  transform: translateY(-1px);
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.2);
}
.nav-links .nav-link.active {
  color: #ffffff;
  background: rgba(99, 102, 241, 0.25);
  box-shadow: 0 1px 4px rgba(99, 102, 241, 0.3);
}

.intro {
  min-height: calc(100vh - 120px);
  display: grid;
  place-items: center;
  background: #1a1d29;
}

.intro-inner {
  width: min(100% - 2rem, 950px);
  text-align: center;
}

.intro-title {
  margin: 0 0 0.5rem;
  font-size: clamp(2rem, 1.5rem + 3vw, 3.2rem);
  line-height: 1.15;
}

.intro-subtitle {
  color: #8b95a8;
  margin-bottom: 2rem;
}

.intro-cta {
  display: flex;
  gap: 1rem;
  justify-content: center;
}

.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  padding: 0.9rem 1.1rem;
  border-radius: 4px;
  font-weight: 600;
  text-decoration: none;
  cursor: pointer;
  border: 1px solid transparent;
  transition: all 0.2s ease;
}
.btn:active {
  transform: translateY(1px);
}

.btn.primary {
  background: #6366f1;
  color: #ffffff;
}
.btn.primary:hover {
  background: #9294f5;
}

.btn.ghost {
  border-color: rgba(255, 255, 255, 0.2);
  color: #e8ecf4;
}
.btn.ghost:hover {
  background: rgba(255, 255, 255, 0.1);
}

/* ABOUT — minimal two lines over background image */
.about.hero-bg {
  padding: 0;
  min-height: 52vh;
  display: grid;
  place-items: center;
  text-align: center;
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
}
.about.hero-bg .about-overlay {
  padding: 3rem 1rem;
  width: min(100% - 2rem, 900px);
  background: rgba(0, 0, 0, 0.45);
  border-radius: 14px;
}
.about.hero-bg .about-line {
  margin: 0;
  font-size: clamp(1rem, 1rem + 1.0vw, 2.0rem);
  line-height: 1.2;
  color: #fff;
}
.about.hero-bg .about-subline {
  margin: 0.75rem 0 0;
  font-size: clamp(0.6rem, .7rem + .5vw, 1.1rem);
  color: rgba(231, 236, 245, 0.9);
}

@media (max-width: 720px) {
  .about.hero-bg {
    background-attachment: scroll;
    min-height: 46vh;
  }
}
/* ===== EXPERIENCE GALLERY (logos) ===== */
.experience .grid.logos {
  display: grid;
  grid-template-columns: repeat(3, minmax(0, 1fr));
  gap: 2rem;
  justify-items: center;
}
.experience .tile {
  background: rgba(255, 255, 255, 0.04);
  border: 1px solid rgba(255, 255, 255, 0.08);
  border-radius: 4px;
  padding: 2rem 1.5rem;
  cursor: pointer;
  transition: all 0.25s ease;
  width: 100%;
  max-width: 320px;
  text-align: center;
}
.experience .tile img {
  width: 160px;
  height: auto;
  display: block;
  margin: 0 auto;
  transition: transform 0.25s ease;
}
.experience .tile figcaption {
  margin-top: 1rem;
  display: grid;
  gap: 0.25rem;
}
.experience .tile figcaption .company {
  font-weight: 800;
  font-size: 1.15rem;
}
.experience .tile figcaption .role {
  color: #8b95a8;
}
.experience .tile figcaption .dates {
  color: #8b95a8;
  font-size: 0.9rem;
}
.experience .tile:hover {
  transform: translateY(-4px);
  box-shadow: 0 8px 24px rgba(0, 0, 0, 0.2);
  border-color: rgba(255, 255, 255, 0.15);
}
.experience .tile:hover img {
  transform: scale(1.05);
}
@media (max-width: 900px) {
  .experience .grid.logos {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (max-width: 600px) {
  .experience .grid.logos {
    grid-template-columns: 1fr;
  }
  .experience .tile img {
    width: 180px;
  }
}

/* Optional: modal meta text */
.modal .meta {
  color: #8b95a8;
  margin: 0.2rem 0 0.6rem;
}

.projects .carousel-container {
  position: relative;
  display: flex;
  align-items: center;
  gap: 1rem;
  max-width: 1000px;
  margin: 0 auto;
}
.projects .carousel-wrapper {
  flex: 1;
  overflow: hidden;
  border-radius: 8px;
  border: 1px solid rgba(255, 255, 255, 0.08);
}
.projects .carousel-slides {
  display: flex;
  transition: transform 0.5s ease;
  width: 300%;
}
.projects .slide {
  width: 33.333%;
  padding: 3rem 2.5rem;
  text-align: center;
  min-height: 320px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 1.5rem;
}
.projects .slide h3 {
  font-size: 1.6rem;
  font-weight: 600;
  margin: 0;
  color: #e8ecf4;
}
.projects .slide p {
  font-size: 1.1rem;
  line-height: 1.7;
  color: #8b95a8;
  max-width: 500px;
  margin: 0;
}
.projects .slide.slide-1 {
  background: rgba(99, 102, 241, 0.05);
}
.projects .slide.slide-2 {
  background: rgba(139, 92, 246, 0.05);
}
.projects .slide.slide-3 {
  background: rgba(16, 185, 129, 0.05);
}
.projects .carousel-btn {
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.1);
  color: #e8ecf4;
  font-size: 1.5rem;
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  cursor: pointer;
  transition: all 0.3s ease;
  -webkit-user-select: none;
     -moz-user-select: none;
          user-select: none;
}
.projects .carousel-btn:hover {
  background: rgba(255, 255, 255, 0.1);
  transform: scale(1.05);
}

.capabilities {
  background: #252a3a;
  padding: 4rem 0;
}
.capabilities .capabilities-subtitle {
  text-align: center;
  font-size: 1.2rem;
  color: #8b95a8;
  margin-bottom: 3rem;
  max-width: 600px;
  margin-left: auto;
  margin-right: auto;
}
.capabilities .capabilities-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
  max-width: 1000px;
  margin: 0 auto;
}
.capabilities .capability-card {
  background: rgba(255, 255, 255, 0.02);
  border: 1px solid rgba(255, 255, 255, 0.08);
  border-radius: 8px;
  padding: 2.5rem 2rem;
  text-align: center;
  transition: all 0.3s ease;
}
.capabilities .capability-card:hover {
  transform: translateY(-4px);
  border-color: rgba(255, 255, 255, 0.15);
  background: rgba(255, 255, 255, 0.04);
}
.capabilities .capability-icon {
  font-size: 2.5rem;
  margin-bottom: 1rem;
  display: block;
}
.capabilities h3 {
  font-size: 1.4rem;
  font-weight: 600;
  color: #e8ecf4;
  margin-bottom: 1rem;
}
.capabilities p {
  color: #8b95a8;
  line-height: 1.6;
  font-size: 1rem;
  margin: 0;
}
@media (max-width: 1024px) {
  .capabilities .capabilities-grid {
    grid-template-columns: 1fr;
    gap: 1.5rem;
  }
  .capabilities .capability-card {
    padding: 2rem 1.5rem;
  }
}
@media (max-width: 720px) {
  .capabilities {
    padding: 3rem 0;
  }
  .capabilities .capabilities-subtitle {
    font-size: 1.1rem;
    margin-bottom: 2rem;
  }
  .capabilities .capability-card {
    padding: 1.5rem 1rem;
  }
  .capabilities .capability-icon {
    font-size: 2rem;
  }
  .capabilities h3 {
    font-size: 1.2rem;
  }
}

.video-section video {
  width: 100%;
  max-width: 900px;
  border-radius: 12px;
  outline: 1px solid rgba(255, 255, 255, 0.12);
}

/* ===== CONTACT ===== */
.contact-min {
  background: #252a3a;
}
.contact-min .lead {
  font-size: clamp(0.8rem, 1rem + 0.5vw, 1.2rem);
  margin: 0.25rem 0 1rem;
  color: #8b95a8;
}
.contact-min .contact-actions {
  display: flex;
  flex-wrap: wrap;
  gap: 0.75rem;
  justify-content: center;
  margin-top: 0.5rem;
}
.contact-min .contact-chip {
  display: inline-flex;
  align-items: center;
  gap: 0.55rem;
  padding: 0.7rem 1rem;
  border-radius: 4px;
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.1);
  text-decoration: none;
  color: #e8ecf4;
  font-weight: 600;
  transition: all 0.2s ease;
}
.contact-min .contact-chip .icon {
  width: 20px;
  height: 20px;
  fill: currentColor;
}
.contact-min .contact-chip:hover {
  transform: translateY(-2px);
  background: rgba(255, 255, 255, 0.08);
}
@media (max-width: 600px) {
  .contact-min .contact-chip {
    width: 100%;
    justify-content: center;
  }
}

.site-footer {
  padding: 2rem 0;
}
.site-footer .container {
  width: min(100% - 2rem, 1400px);
  margin-inline: auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 1rem;
}
.site-footer .to-top {
  text-decoration: none;
  background: rgba(255, 255, 255, 0.08);
  padding: 0.4rem 0.6rem;
  border-radius: 4px;
}

.modal {
  position: fixed;
  inset: 0;
  display: grid;
  place-items: center;
  padding: 1rem;
  background: rgba(0, 0, 0, 0.55);
  opacity: 0;
  pointer-events: none;
  transition: opacity 0.2s ease;
}
.modal .modal-dialog {
  width: min(100% - 2rem, 720px);
  background: #252a3a;
  color: #e8ecf4;
  border-radius: 4px;
  padding: 1.5rem 1.25rem;
  box-shadow: 0 30px 60px rgba(0, 0, 0, 0.35);
  transform: translateY(10px);
  transition: transform 0.2s ease;
}
.modal.open {
  opacity: 1;
  pointer-events: auto;
}
.modal.open .modal-dialog {
  transform: translateY(0);
}
.modal .modal-close {
  position: absolute;
  top: 10px;
  right: 18px;
  font-size: 1.6rem;
  background: transparent;
  color: #8b95a8;
  border: none;
  cursor: pointer;
}
.modal .modal-close:hover {
  color: #e8ecf4;
}

@media (max-width: 1024px) {
  .capabilities .capabilities-grid {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (max-width: 720px) {
  .nav {
    height: 80px;
  }

  .nav-links {
    justify-content: center;
    flex-wrap: wrap;
  }

  .intro {
    min-height: calc(100vh - 80px);
  }

  .capabilities .capabilities-grid {
    grid-template-columns: 1fr;
  }
