AJM;EACE,WAAA;EACA,YAAA;EACA,kBAAA;AAMR;AAHM;EACE,2BAAA;EACA,qCAAA;AAKR;AADI;EACE;IAAgB,WAAA;IAAa,uBAAA;EAKjC;AACF;;AAAA;EACI,eAAA;AAGJ;AADI;EA9cF,+BAAA;EACA,mBAAA;EA+cI,aAAA;EACA,8BAAA;EACA,mBAAA;EACA,SAAA;AAIN;AADI;EACE,qBAAA;EACA,qCAAA;EACA,sBAAA;EACA,kBAAA;AAGN;;AAEA;EACI,eAAA;EACA,QAAA;EACA,aAAA;EACA,mBAAA;EACA,aAAA;EACA,+BAAA;EACA,UAAA;EACA,oBAAA;EACA,6BAAA;AACJ;AACI;EACE,8BAAA;EACA,mBAtfI;EAufJ,cArfC;EAsfD,kBAAA;EACA,uBAAA;EACA,2CAAA;EACA,2BAAA;EACA,+BAAA;AACN;AAEI;EACE,UAAA;EACA,oBAAA;AAAN;AAEM;EACE,wBAAA;AAAR;AAII;EACE,kBAAA;EACA,SAAA;EACA,WAAA;EACA,iBAAA;EACA,uBAAA;EACA,cA7gBE;EA8gBF,YAAA;EACA,eAAA;AAFN;AAIM;EACE,cAjhBD;AA+gBP;;AAQA;EACE;IACE,qCAAA;EALF;AACF;AAQA;EACE;IACE,YA1hBQ;EAohBV;;EASA;IACE,uBAAA;IACA,eAAA;EANF;;EASA;IACE,8BAAA;EANF;;EASA;IACE,0BAAA;EANF;AACF","sourcesContent":["// ===== Variables =====\n$bg: #1a1d29;\n$surface: #252a3a;\n$muted: #8b95a8;\n$text: #e8ecf4;\n$accent: #6366f1;\n\n$maxw: 1400px;\n$nav-large: 120px;\n$nav-small: 80px;\n\n@mixin container {\n  width: min(100% - 2rem, #{$maxw});\n  margin-inline: auto;\n}\n\n@mixin button {\n  display: inline-flex; align-items: center; justify-content: center;\n  gap: .5rem; padding: .9rem 1.1rem; border-radius: 4px;\n  font-weight: 600; text-decoration: none; cursor: pointer;\n  border: 1px solid transparent;\n  transition: all .2s ease;\n  &:active { transform: translateY(1px); }\n}\n\n// ===== Global =====\n* { box-sizing: border-box; }\nhtml { scroll-behavior: smooth; }\nbody {\n  margin: 0;\n  font-family: -apple-system, BlinkMacSystemFont, \"Helvetica Neue\", Helvetica, Arial, sans-serif;\n  color: $text;\n  background: $bg;\n}\na { color: inherit; }\n\n.stripe {\n  padding: 5rem 0;\n  .container { @include container; text-align: center; }\n  h2 { font-size: clamp(1.6rem, 1.5rem + 1vw, 2.2rem); margin: 0 0 1rem; }\n  p { color: $muted; line-height: 1.7; }\n}\n\n// Navbar\n.site-header { \n    position: sticky; \n    top: 0; \n    z-index: 1000; \n    background: rgba(0, 0, 0, 0.9);\n    backdrop-filter: saturate(120%) blur(8px);\n    border-bottom: 1px solid rgba(99, 102, 241, 0.3);\n}\n.nav {\n  @include container;\n  position: relative;\n  display: flex; \n  justify-content: center;\n  align-items: center; \n  gap: 1rem;\n  height: $nav-large; \n  transition: height .25s ease, padding .25s ease;\n  &.shrink { height: $nav-small; }\n}\n.nav .nav-links .nav-link { transition: all .3s ease; }\n.nav.shrink .nav-links .nav-link { \n  font-size: clamp(0.8rem, .7rem + .2vw, 1rem); \n  padding: .4rem .7rem; \n}\n  \n.nav-links {\n  list-style: none; margin: 0; padding: 0;\n  display: flex; justify-content: center; gap: 0.5rem; align-items: center;\n  flex-wrap: wrap;\n  .nav-link {\n    text-decoration: none; padding: .5rem .8rem; border-radius: 4px;\n    font-size: clamp(0.9rem, 0.8rem + .2vw, 1.1rem); \n    font-weight: 600;\n    text-transform: uppercase;\n    letter-spacing: 0.3px;\n    color: #ffffff;\n    white-space: nowrap;\n    transition: all .3s ease;\n    &:hover { \n      color: #ffffff; \n      background: rgba(255,255,255,.15); \n      transform: translateY(-1px);\n      box-shadow: 0 2px 8px rgba(0,0,0,.2);\n    }\n    &.active { \n      color: #ffffff; \n      background: rgba(99, 102, 241, .25); \n      box-shadow: 0 1px 4px rgba(99, 102, 241, .3);\n    }\n  }\n}\n\n// ===== Hero Section =====\n.intro {\n  min-height: calc(100vh - #{$nav-large});\n  display: grid; place-items: center;\n  background: $bg;\n}\n.intro-inner { \n    width: min(100% - 2rem, 950px); \n    text-align: center; \n}\n.intro-title { \n    margin: 0 0 .5rem; \n    font-size: clamp(2rem, 1.5rem + 3vw, 3.2rem); \n    line-height: 1.15;\n}\n.intro-subtitle { \n    color: $muted;\n    margin-bottom: 2rem; \n}\n.intro-cta { \n    display: flex; \n    gap: 1rem; \n    justify-content: center; \n}\n\n.btn { @include button; }\n.btn.primary { \n    background: $accent; \n    color: #ffffff; \n    &:hover { background: lighten($accent, 10%); } \n}\n.btn.ghost { \n    border-color: rgba(255,255,255,.2); \n    color: $text; \n    &:hover { background: rgba(255,255,255,.1); } \n}\n\n\n\n/* ABOUT — minimal two lines over background image */\n.about.hero-bg {\n    padding: 0;\n    min-height: 52vh;\n    display: grid;\n    place-items: center;\n    text-align: center;\n    background-image:\n      url(\"../assets/background.png\");\n    background-size: cover;\n    background-position: center;\n    background-attachment: fixed;\n  \n    .about-overlay {\n      padding: 3rem 1rem;\n      width: min(100% - 2rem, 900px);\n      background: rgba(0, 0, 0, 0.45); \n      border-radius: 14px;\n    //   backdrop-filter: blur(8px); // Optional: adds a nice blur effect\n    }\n  \n    .about-line {\n      margin: 0;\n      font-size: clamp(1rem, 1rem + 1.0vw, 2.0rem);\n      line-height: 1.2;\n      color: #fff;\n    }\n  \n    .about-subline {\n      margin: .75rem 0 0;\n      font-size: clamp(0.6rem, .7rem + .5vw, 1.1rem);\n      color: rgba(231, 236, 245, 0.9);\n    }\n  }\n\n  @media (max-width: 720px) {\n    .about.hero-bg {\n      background-attachment: scroll;\n      min-height: 46vh;\n    }\n  }\n  \n\n/* ===== EXPERIENCE GALLERY (logos) ===== */\n.experience {\n    .grid.logos {\n      display: grid;\n      grid-template-columns: repeat(3, minmax(0, 1fr));\n      gap: 2rem;\n      justify-items: center;    // center tiles themselves\n    }\n  \n    .tile {\n      background: rgba(255,255,255,.04);\n      border: 1px solid rgba(255,255,255,.08);\n      border-radius: 4px;\n      padding: 2rem 1.5rem;\n      cursor: pointer;\n      transition: all .25s ease;\n      width: 100%;\n      max-width: 320px;\n      text-align: center;\n  \n      img {\n        width: 160px;\n        height: auto;\n        display: block;\n        margin: 0 auto;\n        transition: transform .25s ease;\n      }\n  \n      figcaption {\n        margin-top: 1rem;\n        display: grid;\n        gap: .25rem;\n  \n        .company { font-weight: 800; font-size: 1.15rem; }\n        .role    { color: $muted; }\n        .dates   { color: $muted; font-size: .9rem; }\n      }\n  \n      &:hover {\n        transform: translateY(-4px);\n        box-shadow: 0 8px 24px rgba(0,0,0,.2);\n        border-color: rgba(255,255,255,.15);\n  \n        img {\n          transform: scale(1.05);\n        }\n      }\n    }\n  \n    @media (max-width: 900px) {\n      .grid.logos { grid-template-columns: repeat(2, 1fr); }\n    }\n    @media (max-width: 600px) {\n      .grid.logos { grid-template-columns: 1fr; }\n      .tile img { width: 180px; }\n    }\n  }\n  \n  \n  \n  /* Optional: modal meta text */\n  .modal .meta {\n    color: $muted;\n    margin: .2rem 0 .6rem;\n  }\n  \n  \n  \n// ===== Work (carousel) =====\n.projects {\n  .carousel-container {\n    position: relative;\n    display: flex;\n    align-items: center;\n    gap: 1rem;\n    max-width: 1000px;\n    margin: 0 auto;\n  }\n\n  .carousel-wrapper {\n    flex: 1;\n    overflow: hidden;\n    border-radius: 8px;\n    border: 1px solid rgba(255, 255, 255, 0.08);\n  }\n\n  .carousel-slides {\n    display: flex;\n    transition: transform 0.5s ease;\n    width: 300%;\n  }\n\n  .slide {\n    width: 33.333%;\n    padding: 3rem 2.5rem;\n    text-align: center;\n    min-height: 320px;\n    display: flex;\n    flex-direction: column;\n    justify-content: center;\n    align-items: center;\n    gap: 1.5rem;\n\n    h3 {\n      font-size: 1.6rem;\n      font-weight: 600;\n      margin: 0;\n      color: $text;\n    }\n\n    p {\n      font-size: 1.1rem;\n      line-height: 1.7;\n      color: $muted;\n      max-width: 500px;\n      margin: 0;\n    }\n\n    &.slide-1 { background: rgba(99, 102, 241, 0.05); }\n    &.slide-2 { background: rgba(139, 92, 246, 0.05); }\n    &.slide-3 { background: rgba(16, 185, 129, 0.05); }\n  }\n\n  .carousel-btn {\n    background: rgba(255, 255, 255, 0.05);\n    border: 1px solid rgba(255, 255, 255, 0.1);\n    color: $text;\n    font-size: 1.5rem;\n    width: 40px;\n    height: 40px;\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    border-radius: 50%;\n    cursor: pointer;\n    transition: all 0.3s ease;\n    user-select: none;\n\n    &:hover { \n      background: rgba(255, 255, 255, 0.1); \n      transform: scale(1.05);\n    }\n  }\n}\n  \n\n.capabilities {\n  background: $surface;\n  padding: 4rem 0;\n  \n  .capabilities-subtitle {\n    text-align: center;\n    font-size: 1.2rem;\n    color: $muted;\n    margin-bottom: 3rem;\n    max-width: 600px;\n    margin-left: auto;\n    margin-right: auto;\n  }\n  \n  .capabilities-grid {\n    display: grid;\n    grid-template-columns: repeat(2, 1fr);\n    gap: 2rem;\n    max-width: 1000px;\n    margin: 0 auto;\n  }\n  \n  .capability-card {\n    background: rgba(255, 255, 255, 0.02);\n    border: 1px solid rgba(255, 255, 255, 0.08);\n    border-radius: 8px;\n    padding: 2.5rem 2rem;\n    text-align: center;\n    transition: all 0.3s ease;\n    \n    &:hover {\n      transform: translateY(-4px);\n      border-color: rgba(255, 255, 255, 0.15);\n      background: rgba(255, 255, 255, 0.04);\n    }\n  }\n  \n  .capability-icon {\n    font-size: 2.5rem;\n    margin-bottom: 1rem;\n    display: block;\n  }\n  \n  h3 {\n    font-size: 1.4rem;\n    font-weight: 600;\n    color: $text;\n    margin-bottom: 1rem;\n  }\n  \n  p {\n    color: $muted;\n    line-height: 1.6;\n    font-size: 1rem;\n    margin: 0;\n  }\n  \n  @media (max-width: 1024px) {\n    .capabilities-grid {\n      grid-template-columns: 1fr;\n      gap: 1.5rem;\n    }\n    \n    .capability-card {\n      padding: 2rem 1.5rem;\n    }\n  }\n  \n  @media (max-width: 720px) {\n    padding: 3rem 0;\n    \n    .capabilities-subtitle {\n      font-size: 1.1rem;\n      margin-bottom: 2rem;\n    }\n    \n    .capability-card {\n      padding: 1.5rem 1rem;\n    }\n    \n    .capability-icon {\n      font-size: 2rem;\n    }\n    \n    h3 {\n      font-size: 1.2rem;\n    }\n  }\n}\n  \n  \n  \n\n\n// ===== Video =====\n.video-section video { width: 100%; max-width: 900px; border-radius: 12px; outline: 1px solid rgba(255,255,255,.12); }\n\n/* ===== CONTACT ===== */\n.contact-min {\n    background: $surface;\n  \n    .lead {\n        font-size: clamp(0.8rem, 1rem + 0.5vw, 1.2rem);\n        margin: .25rem 0 1rem;\n        color: $muted;\n    }\n  \n    .contact-actions {\n      display: flex;\n      flex-wrap: wrap;\n      gap: .75rem;\n      justify-content: center;\n      margin-top: .5rem;\n    }\n  \n    .contact-chip {\n      display: inline-flex;\n      align-items: center;\n      gap: .55rem;\n      padding: .7rem 1rem;\n      border-radius: 4px;\n      background: rgba(255,255,255,.05);\n      border: 1px solid rgba(255,255,255,.10);\n      text-decoration: none;\n      color: $text;\n      font-weight: 600;\n      transition: all .2s ease;\n\n      .icon {\n        width: 20px;\n        height: 20px;\n        fill: currentColor;\n      }\n\n      &:hover { \n        transform: translateY(-2px); \n        background: rgba(255,255,255,.08); \n      }\n    }\n  \n    @media (max-width: 600px) {\n      .contact-chip { width: 100%; justify-content: center; }\n    }\n  }\n  \n\n// ===== Footer =====\n.site-footer {\n    padding: 2rem 0;\n  \n    .container {\n      @include container;\n      display: flex;\n      justify-content: space-between;\n      align-items: center;\n      gap: 1rem;\n    }\n  \n    .to-top {\n      text-decoration: none;\n      background: rgba(255, 255, 255, 0.08);\n      padding: 0.4rem 0.6rem;\n      border-radius: 4px;\n    }\n  }\n\n// ===== Modal =====\n.modal {\n    position: fixed;\n    inset: 0;\n    display: grid;\n    place-items: center;\n    padding: 1rem;\n    background: rgba(0, 0, 0, 0.55);\n    opacity: 0;\n    pointer-events: none;\n    transition: opacity 0.2s ease;\n  \n    .modal-dialog {\n      width: min(100% - 2rem, 720px);\n      background: $surface;\n      color: $text;\n      border-radius: 4px;\n      padding: 1.5rem 1.25rem;\n      box-shadow: 0 30px 60px rgba(0, 0, 0, 0.35);\n      transform: translateY(10px);\n      transition: transform 0.2s ease;\n    }\n  \n    &.open {\n      opacity: 1;\n      pointer-events: auto;\n  \n      .modal-dialog {\n        transform: translateY(0);\n      }\n    }\n  \n    .modal-close {\n      position: absolute;\n      top: 10px;\n      right: 18px;\n      font-size: 1.6rem;\n      background: transparent;\n      color: $muted;\n      border: none;\n      cursor: pointer;\n  \n      &:hover {\n        color: $text;\n      }\n    }\n  }\n\n// ===== Responsive =====\n@media (max-width: 1024px) {\n  .capabilities .capabilities-grid {\n    grid-template-columns: repeat(2, 1fr);\n  }\n}\n\n@media (max-width: 720px) {\n  .nav {\n    height: $nav-small;\n  }\n  \n  .nav-links {\n    justify-content: center;\n    flex-wrap: wrap;\n  }\n  \n  .intro {\n    min-height: calc(100vh - #{$nav-small});\n  }\n  \n  .capabilities .capabilities-grid {\n    grid-template-columns: 1fr;\n  }\n}"],"sourceRoot":""}]);
